export default {
  layout: 'home',
  data() {
    return {
      text: '',
    }
  },
  mounted() {
    console.log('Я открылся))))')
    this.socket = this.$nuxtSocket({
      channel: '/socket-io',
    })
  },
  methods: {
    SocketIo(text) {
      this.socket.emit(
        'text',
        {
          text: text,
        },
        (res) => {
          console.log(res.data.text)
        }
      )
    },
  },
}
